<template>
  <div>
    <van-nav-bar title="文章详情" @click-left='$router.back()'>
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>
    <!-- 加载页面 -->
    <van-loading size="24px" vertical v-if="islodaing">加载中...</van-loading>
    <!-- 显示正文 -->
    <zhengwen :artid="art_id"  :art='art' v-else-if="issuccess"></zhengwen>
    <!-- 请求失败 -->
    <div class="fail" v-else>
      <span>请求失败</span>
      <van-button plain type="info" @click="getart()">刷新</van-button>
    </div>
  </div>
</template>

<script>
import { getarticle } from '@/api/new'
import zhengwen from './components/zhengwen'
export default {
  name: 'artical',
  data () {
    return {
      islodaing: true,
      issuccess: true,
      art: {}
    }
  },
  props: ['art_id'],
  methods: {
    changeloading () {
      console.log('1111111111')
    },
    async getart () {
      try {
        let res = await getarticle({ art_id: this.art_id })
        console.log(res.data)
        this.art = res.data
        // JSON.parse('ada')

        this.issuccess = true
      } catch (err) {
        console.log(err)
        this.issuccess = false
      }
      this.islodaing = false
    }
  },
  created () {
    this.getart()
  },
  components: {
    zhengwen
  }
}
</script>

<style lang='less' scoped>
.van-icon {
  color: #fff;
}
.fail {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
</style>
