import request from '@/utils/request'

export const pindao = () => {
  return request({
    url: '/app/v1_0/user/channels'

  })
}

export const pindaoNews = (data) => {
  return request({
    url: '/app/v1_1/articles',
    params: data

  })
}
export const allpds = (data) => {
  return request({
    url: '/app/v1_0/channels',
    params: data

  })
}
export const getarticle = (data) => {
  return request({
    url: '/app/v1_0/articles/' + data.art_id

  })
}
